import "./News.css";
import NewsCard from "./NewsCard";
import NewsBase from "./NewsBase";

const News = ({ activeNews, homePage }) => {
  return (
    <section className={activeNews ? "news news-click-style" : "news"}>
      <h2 data-aos="fade-right" data-aos-duration="600" data-aos-once="true">
        Yangiliklar
      </h2>
      <div className="row align-items-start">
        {homePage
          ? [NewsBase[0], NewsBase[1], NewsBase[2]].map((item) => (
              <NewsCard
                key={item.id}
                id={item.id}
                imgUrl={item.imgUrl}
                title={item.title}
                date={item.date}
                fullInfo={item.fullInfo}
                aos={item.aos}
                aosDelay={item.aosDelay}
              />
            ))
          : NewsBase.map((item) => (
              <NewsCard
                key={item.id}
                id={item.id}
                imgUrl={item.imgUrl}
                title={item.title}
                date={item.date}
                fullInfo={item.fullInfo}
                aos={item.aos}
                aosDelay={item.aosDelay}
              />
            ))}
      </div>
    </section>
  );
};

export default News;
